const baseURL = 'https://api.qq.jsososo.com'


const request = {
	get(url, data) {

		return new Promise((resolve, reject) => {

			uni.request({
				url: baseURL + url,
				data: data,
				header: {
					'Accept': 'application/json',
					'Content-Type': 'application/x-www-form-urlencoded', //
				},
				method: "GET",
				success: (response) => {
					let res = response.data
					if (res.result === 100) {
						resolve(res)
					} else {
						reject(res)
						showError(res);
					}
				},
				fail: (error) => {
					if (error && error.response) {
						reject(error.response)
						showError(error.response);
					}
				},


			});
		})

	},
}

const showError = error => {
	let errorMsg = ''
	switch (error.code) {
		case 400:
			errorMsg = ''
			break
		case 301:
			errorMsg = ''
			break
		case 403:
			errorMsg = ''
			break
		case 404:
			errorMsg = ''
			break
		case 408:
			errorMsg = ''
			break
		case 500:
			errorMsg = ''
			break
		case 501:
			errorMsg = ''
			break
		case 502:
			errorMsg = ''
			break
		case 503:
			errorMsg = ''
			break
		case 504:
			errorMsg = ''
			break
		case 505:
			errorMsg = 'HTTP'
			break
		default:
			errorMsg = ""
			break
	}

	uni.showToast({
		title: errorMsg,
		icon: 'none',
		duration: 3000,
		complete: function() {
			setTimeout(function() {
				uni.hideToast();
			}, 3000);
		}
	});
}

export default request
